html,body{
	overflow-x: hidden;
}
.navbar{
	min-width: 97vw;
	display: flex;
	flex-direction: row;
	background-color: #bebebe;
	border: solid black 2px;
}

.navbar a{
	text-align: center;
}
.navbloc{
	padding: 10px 10px 10px 10px;
	border-right: solid black 1px;
	border-left: solid black 1px;
	
	text-decoration: none;
	font-weight: bold;
	color: black;
	width: 16.666%;
}

.navbloc:hover{
	background-color: rgb(255, 169, 255);
}

.active{
	background-color: rgb(214, 4, 214);
}

@media (max-width: 600px){
	.navbar{
		display: flex;
		flex-direction: column;
	}
	.navbloc{
		padding: 10px 10px 10px 10px;
		border-right: none;
		border-left: none;
		border-top: solid black 1px;
		border-bottom: solid black 1px;
		width: 100%;
	}

}