/*Common Page*/
.pads{
	padding: 0px 25px 25px 25px;
}
.card-column{
	display: flex;
	flex-direction: column;
}
.card-row{
	display: flex;
	flex-direction: row;
}

.card-smaller{
	display: flex;
	flex-direction: row;
	width: 100%;
}

.cards{
	align-items: center;
}

.card{
	padding: 10px;
	margin-bottom: 25px;

	border: black solid 3px;
	border-radius: 10px;
	background-color: #bebebe;

	color:black;
	font-size: large;
	font-weight: 600;
	width: 75%;
}

.card-info{
	width: 100%;
	padding: 0px 5px 0px 5px;
	
}
.card-title-link{
	justify-content: space-between;
	width: 100%;
}
.headline{
	text-align: center;
}
.link{
	border: solid 2px black;
	border-radius: 5px;
	height: 50px;
	width: 100px;
	margin-left: 10px;

	background-color: #3f3f3f;
	text-decoration: none;
	color: white;
}
.card img{
	border: 3px solid black;
	width: 400px;
}

/* Home Page */
.contact{
	justify-content: space-between;
	margin: auto auto 50px auto;
	width: 50%;
}



/*Asset Gallery*/
.card-grid {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 5px;
	align-items: start;
}
.card-gallery{
	width:90%;
}

.card-gallery img {
	width: 97%;
}

/*Resizing - MUST BE LAST*/
@media (max-width: 900px) {

	.card-smaller{
		display: flex;
		flex-direction: column;
	}

	.card p{
		font-size: medium;
	}

	.headline{
			font-size: x-large;
	}

	.card-gallery{
		width: 75%
	}
}

@media (max-width: 600px){

	.headline{
		font-size: large;
	}
	.contact{
		width: 100%;
	}

	.card img{
		margin-bottom: 10px;
		width: 97%;
	}
	.card-grid {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 5px;
	}
}
